.MenuToggle {
    position: fixed;
    top: 16px;
    left: 16px;
    padding: 10px;
    font-size: 20px;
    cursor: pointer;
    color: #fff;
    z-index: 100;
    -webkit-transition: opacity .22s ease-in, left .22s ease-in;
    -o-transition: opacity .22s ease-in, left .22s ease-in;
    transition: opacity .22s ease-in, left .22s ease-in;
}

.MenuToggle:hover {
    opacity: .7;
}

.MenuToggle.open {
    left: 320px;
}

@media (max-width: 375px) {
    .MenuToggle.open {
        color: #363d54;
        left: auto;
        right: 20px;
    }
}




