.Loader {
    text-align: center;
}

.Loader > div {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: 40px 40px;
        -ms-transform-origin: 40px 40px;
            transform-origin: 40px 40px;
}

.Loader > div div {
    top: 32px;
    left: 32px;
    position: absolute;
    width: 32px;
    height: 32px;
    background: #fff;
    -webkit-animation: loader 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
            animation: loader 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}
.Loader > div div:after,
.Loader > div div:before {
    content: " ";
    position: absolute;
    display: block;
    width: 32px;
    height: 32px;
    background: #fff;
}
.Loader > div div:before {
    left: -24px;
    border-radius: 50% 0 0 50%;
}
.Loader > div div:after {
    top: -24px;
    border-radius: 50% 50% 0 0;
}
@-webkit-keyframes loader {
    0% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
    }
    5% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    39% {
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
    }
    45% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
    }
    100% {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
}
@keyframes loader {
    0% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
    }
    5% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    39% {
        -webkit-transform: scale(0.85);
                transform: scale(0.85);
    }
    45% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(0.95);
                transform: scale(0.95);
    }
    100% {
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
}



