.QuizList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 100px 16px 100px 16px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: -webkit-gradient(linear, left top, right top, from(#fd8355), color-stop(36%, #f0576c), to(#f79cbd));
    background: -webkit-linear-gradient(left, #fd8355 0%, #f0576c 36%, #f79cbd 100%);
    background: -o-linear-gradient(left, #fd8355 0%, #f0576c 36%, #f79cbd 100%);
    background: linear-gradient(90deg, #fd8355 0%, #f0576c 36%, #f79cbd 100%);

    -ms-overflow-style: none; /* Internet Explorer 10+ */
    scrollbar-width: none;

}

.QuizList::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    background: transparent; /* make scrollbar transparent */
}

.QuizList h1, h2 {
    text-align: center;
    color: #fff;
}

.QuizList ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.QuizList li {
    padding: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: background-color 0.3s;
    -o-transition: background-color 0.3s;
    transition: background-color 0.3s;
    border-radius: 5px;
}

.QuizList li:hover {
    background-color: rgba(90, 90, 90, 0.1);
}

.QuizList li a {
    display: inline-block;
    margin: -5px 5px -5px -5px;
    padding: 5px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    min-width: 0;
    text-decoration: none;
    color: #fff;
    -webkit-transition: color 0.3s;
    -o-transition: color 0.3s;
    transition: color 0.3s;
}

.QuizListItemName {
    display: inline-block;
    max-width: 215px;
}

.Sorting {
    margin-bottom: 10px;
    padding: 5px 7px 5px 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row-reverse;
        flex-direction: row-reverse;
    -ms-flex-align: center;
        align-items: center;
}

.Sorting button {
    width: 27px;
    height: 27px;
    border: 1px solid #ffffff;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.QuizList .Sorting i {
    color: #ffffff;
    font-size: 12px;
}

.Sorting span {
    margin-right: 0.8em;
    font-size: 16px;
    opacity: 0;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
}

.Sorting button:hover + span {
    opacity: 0.8;
}

.QuizList li div {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.QuizList li div span {
    margin-right: 0.2em;
}

.QuizListIcon {
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-in-out;
    -o-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out;
}

.QuizListIcon + .QuizListIcon {
    margin-left: 7px;
}

.QuizList li:hover .QuizListIcon,
.QuizListIcon[aria-pressed="true"]{
    opacity: 1;
}

@media (max-width: 720px) {
    .QuizList {
        padding-top: 50px;
    }

    .QuizListIcon {
        opacity: 1;
    }

    .Sorting span {
        visibility: hidden;
    }

    .QuizList li div span {
        display: none;
    }
}



