.Drawer {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    padding: 20px 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: rgba(255, 255, 255, 0.1);
    z-index: 90;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    -webkit-backdrop-filter: blur(2px);
            backdrop-filter: blur(2px);
}

.Drawer ul {
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    max-width: 1000px;
    width: 100%;
}

.Drawer ul li:last-child {
    margin-left: auto;
}

.Drawer ul li a {
    color: #fff;
    font-size: 24px;
    text-decoration: none;
    padding: 0 20px 10px 20px;
    -webkit-transition: opacity .3s;
    -o-transition: opacity .3s;
    transition: opacity .3s;
}

.Drawer ul li a i {
    margin-left: 10px;
}

.Drawer ul li a:hover, .active {
    opacity: .7;
}

.Drawer ul li a.active {
    pointer-events: none;
}

@media (max-width: 720px) {
    .Drawer {
        bottom: 0;
        width: 80%;
        padding: 40px 10px;
        max-width: 300px;
        -ms-flex-pack: left;
            justify-content: left;
        background: #fff;
        -webkit-transform: translateX(0);
            -ms-transform: translateX(0);
                transform: translateX(0);
        -webkit-transition: -webkit-transform .22s ease-in;
        transition: -webkit-transform .22s ease-in;
        -o-transition: transform .22s ease-in;
        transition: transform .22s ease-in;
        transition: transform .22s ease-in, -webkit-transform .22s ease-in;
    }

    .Drawer ul li a {
        color: #363d54;
    }

    .Drawer ul {
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .Drawer ul li:last-child {
        margin-left: 0;
        margin-bottom: 0;
    }

    .Drawer.close {
        -webkit-transform: translateX(-300px);
            -ms-transform: translateX(-300px);
                transform: translateX(-300px);
    }

    .Drawer ul li {
        margin-bottom: 15px;
    }
}

@media (max-width: 375px) {
    .Drawer {
        max-width: 100%;
        width: 100%;
    }

    .Drawer.close {
        -webkit-transform: translateX(-100%);
            -ms-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}



